import $               from 'ZEPTO_PATH';
import VARIABLE        from 'UNIT_PATH/variable';
import header          from 'MODULES_PATH/header.js';
import layout          from 'MODULES_PATH/layout';
import redSlider       from 'MODULES_PATH/redSlider';
import FooterAd        from 'MODULES_PATH/footerAd';
import goTop           from 'MODULES_PATH/goTop';
import LazyLoad        from 'MODULES_PATH/lazyLoad';
import userFilterSec   from 'MODULES_PATH/newuserFilterSec';
import newUserGdsort   from 'MODULES_PATH/newUserGdsort';
import newUserGdsList  from 'MODULES_PATH/newUserGdsList';
import lazySuccess     from 'MODULES_PATH/lazySuccess';
import filterFloat     from 'MODULES_PATH/newUserFilterFloat';

//VARIABLE.CONFIG.ANIMA_SCROLL = true;
window.$ = $;

// 页面布局调整
layout();

// 回到顶部按钮
goTop();

// 懒加载
LazyLoad({
	loadSucess: function(img) {
		lazySuccess.loadLogoImg(img, '.gds-wrap');
	}
});

// 公共头部
header.headerInit();

// 轮播图
redSlider.init();

// 尾部广告
var footerAd = new FooterAd();

footerAd.init();

// 筛选滑动
userFilterSec();

// 排序筛选
newUserGdsort();

// 列表翻页包括价格和库存数据
newUserGdsList();

// 筛选导航悬浮
filterFloat();



