function main() {
	var scrollTop, newUserMsgTop, filterBoxHeight;
	var win = $(window);
	var newUserMsg = $('.new-user-msg');
	var filterBox = $('#filter-box');
	var gdsList = $('#gds-list');

	win.bind('scroll', function() {
		scrollTop = win.scrollTop();
		newUserMsgTop = newUserMsg.offset().top + newUserMsg.height();

		if(filterBox.length) {
			filterBoxHeight = filterBox.height();
		}else{
			filterBoxHeight = 0;
		}

		if(scrollTop >= newUserMsgTop) {
			filterBox.addClass('fix');
			gdsList.css('padding-top', filterBoxHeight + 'px');
		}else{
			filterBox.removeClass('fix');
			gdsList.css('padding-top', '0px');
		}
	});
}

export default main;


