import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import model from 'UNIT_PATH/model';
import view from 'UNIT_PATH/view';

var modal_scroller;
var filterBtnData = {
	typeIndex: [0],
	sizeIndex: [0],
	colorIndex: [0]
};
var body_dom = $('body');
var go_top_btn = $('#go-top');

function main() {
	var filter_content_box = $('#filter-content-box').get(0);

	modal_scroller = new anima_ctrl.scrollview(filter_content_box, {});
	modal_scroller.init();
	// 旋转设备时重新计算滚动区域尺寸
	$(window).bind('orientationchange', function() {
		modal_scroller.refresh();
	});

	// 点击排序
	gdSort();

	// 筛选
	filter();

	// 颜色尺码多选
	filterCheckBox();
}

function gdSort() {
	//点击销量排序
	var sale_sort_btn = $('.sale-sort');
	var price_sort = $('.price-sort');

	sale_sort_btn.click(function() {
		var _this = $(this);
		var icon = price_sort.find('.icon-sort');
		var sale_sort_icon = sale_sort_btn.find('.icon-sort');

		price_sort.removeClass('active');
		_this.addClass('active');
		
		// 取消价格排序高亮效果
		icon.removeClass('down').removeClass('up');

		// 开启销量排序高亮效果
	    if(!sale_sort_icon.hasClass('up') && !sale_sort_icon.hasClass('down')) {
	    	// 降序
	    	body_dom.attr('data-sort-flag', '3');
	    	sale_sort_icon.addClass('down');
	    }else if(sale_sort_icon.hasClass('down')) {
	    	// 升序
	    	body_dom.attr('data-sort-flag', '4');
	    	sale_sort_icon.addClass('up');
	    	sale_sort_icon.removeClass('down');
	    }else if(sale_sort_icon.hasClass('up')) {
	    	// 无序
	    	body_dom.attr('data-sort-flag', '0');
	    	sale_sort_icon.removeClass('up');
	    	_this.removeClass('active');
	    }

		sort_request();
	});

	// 点击价格排序
	price_sort.click(function() {
	    var _this = $(this);
	    var _thisIcon = price_sort.find('.icon-sort');

	    sale_sort_btn.removeClass('active');
		_this.addClass('active');

	    // 取消销量排序高亮状态
	    sale_sort_btn.find('.icon-sort').removeClass('down').removeClass('up');

	    if(!_thisIcon.hasClass('up') && !_thisIcon.hasClass('down')) {
	    	// 升序
	    	body_dom.attr('data-sort-flag', '1');
	    	_thisIcon.addClass('up');
	    }else if(_thisIcon.hasClass('up')) {
	    	// 降序
	    	body_dom.attr('data-sort-flag', '2');
	    	_thisIcon.removeClass('up');
	    	_thisIcon.addClass('down');
	    }else if(_thisIcon.hasClass('down')) {
	    	// 无序
	    	body_dom.attr('data-sort-flag', '0');
	    	_thisIcon.removeClass('down');
	    	_this.removeClass('active');
	    }

	    sort_request();
	});
}

function sort_request() {
	var gds = $('.gds-list');
	var ul = gds.find('ul');
	var go_top_btn = $('#go-top');

	VARIABLE.GLOBAL.NEW_USER.getMoreLoading = false;

	model.sortGoods(function(data) {
		go_top_btn.addClass('none');
		ul.html(data);
		body_dom.attr('data-page', '1');
		body_dom.attr('data-total-page', $('.skus').attr('data-total-page'));
		VARIABLE.GLOBAL.LAZY.refreshImg();
		$(window).scrollTop(0);
		getPriceAndStock(1);
	});
}

function filter() {
	var filterModal = $('#filter-modal');
	var gds = $('.gds-list');
	var ul = gds.find('ul');
	var t_start = null;

	// 点击筛选按钮
	$('.filter').on('tap', function() {
		filterModal.addClass('filter-content-show');
		filterModal.removeClass('none');
	});

	// 取消筛选
	$('#filter-modal .btn-text.fl').bind('tap', function() {
		filterModal.removeClass('filter-content-show');
		filterDataReturn();
	});

	// 重置
	$('#filter-modal .btn-text.fr').bind('tap', function() {
		$('.type-group').each(function() {
			$(this).find('li').removeClass('selected').eq(0).addClass('selected');
		});
	});

	// 确认筛选
	$('#filter-modal .btn-comfirm-red').bind('click', function() {
		// if (t_start == null){
	 //    	t_start = new Date().getTime();
	 //    }else{
	 //    	var t_end = new Date().getTime();
		//     if(t_end - t_start < 300){
		// 	    t_start = t_end;
		// 	    return;
		//     }else{
		//     	t_start = t_end;
		//     }
	 //    }
	    $('body').attr('data-filter-now', 'cateIdStr');
		setFilterData();
		filterModal.removeClass('filter-content-show');
		if(!$('.type-row').eq(0).find('li.all').hasClass('selected')) {
			$('.filter').addClass('active').find('.icon-filter').addClass('active');
		}else{
			$('.filter').removeClass('active').find('.icon-filter').removeClass('active');
		}
		
		$('.newuser-filter-sec li').removeClass('active');
		$('.newuser-filter-sec li').eq(0).addClass('active');

		var active_slider = $('.newuser-filter-sec').find('li').get(0);

		swiperTranlateTo(VARIABLE.GLOBAL.NEW_USER.navSwiper, active_slider);
		model.filterConfirm(function(data) {
			go_top_btn.addClass('none');
			ul.html(data);
			body_dom.attr('data-page', '1');
			body_dom.attr('data-total-page', $('.skus').attr('data-total-page'));
			VARIABLE.GLOBAL.LAZY.refreshImg();
			$(window).scrollTop(0);
			getPriceAndStock(1);
		});
	});
}

function getPriceAndStock(page) {
	var skus = $('.skus').eq(page - 1).attr('data-skus');

	model.getPriceData(skus, function(data) {
		view.renderNewUserPrice(skus.split(','), data);
	});
}

function swiperTranlateTo(swiper, active_slider) {
	var swiper_width_half = parseInt(swiper.width / 2, 10);
	var offset_left       = active_slider.offsetLeft;
	var max_translate     = swiper.maxTranslate();
	var min_translate     = swiper.minTranslate();
	var translate_to      = swiper_width_half - offset_left - active_slider.offsetWidth / 2;

	if(translate_to > min_translate) {
		translate_to = min_translate;
	}else if(translate_to < max_translate) {
		translate_to = max_translate;
	}

	swiper.setWrapperTransition(0);
	swiper.setWrapperTranslate(translate_to);
}

function filterCheckBox() {
	var t_start = null;

	$('.filter-content').bind('tap', function(ev) {
		if (t_start == null){
	    	t_start = new Date().getTime();
	    }else{
	    	var t_end = new Date().getTime();
		    if(t_end - t_start < 300){
			    t_start = t_end;
			    return;
		    }else{
		    	t_start = t_end;
		    }
	    }

		var target = $(ev.target);
		var li = target.closest('li');
		var row = li.closest('.type-row');
		var rowIndex = $('.type-row').index(row);

		if(li.length) {
			if(rowIndex == 0) {
				// 获取颜色尺码选项
				row.find('li').removeClass('selected');
				li.addClass('selected');
				//getColorSize();
			}else{
				if(li.hasClass('all')) {
					row.find('li').removeClass('selected');
					li.addClass('selected');
				}else{
					row.find('li.all').removeClass('selected');
					li.hasClass('selected')? li.removeClass('selected'): li.addClass('selected');
				}
			}
		}
	});
}

// 缓存颜色尺码选项
function setFilterData() {
	var type = $('.type-row').eq(0).find('li');

	filterBtnData.typeIndex.length = 0;

	type.each(function(i, v) {
		if($(this).hasClass('selected')) {
			filterBtnData.typeIndex.push(i);
		}
	});
}

// 取消筛选是回到之前选项状态
function filterDataReturn() {
	var type = $('.type-row').eq(0).find('li');

	type.removeClass('selected').eq(filterBtnData.typeIndex[0]).addClass('selected');
}

// 获取颜色和尺码
function getColorSize(callback) {
	model.getColorAndSizeData(function(data) {
		view.renderSizeColor(data);
		modal_scroller && modal_scroller.refresh();
		callback && callback();
	});
}

export default main;




