import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import LAZY_LOAD from 'UNIT_PATH/lazy_load';

function main(opts) {
	// 如果不是ios，就是用系统滚动懒加载
	if(!VARIABLE.CONFIG.ANIMA_SCROLL) {
		VARIABLE.GLOBAL.LAZY = LAZY_LOAD.lazy.init({
			load_sucess: function(img) {
				opts.loadSucess && opts.loadSucess(img);
			},
			loadData: function(item) {
				// 数据懒加载
				opts.loadData && opts.loadData(item);
			}
		});
		return;
	};

	if(!VARIABLE.GLOBAL.SCROLL_OBJ) return;
	VARIABLE.GLOBAL.LAZY = LAZY_LOAD.animaScrollLazy.init({
		iscroller: VARIABLE.GLOBAL.SCROLL_OBJ,
		load_sucess: function(img) {
			opts.loadSucess && opts.loadSucess(img);
		},
		loadData: function(item) {
			// 数据懒加载
			opts.loadData && opts.loadData(item);
		}
	});
}

module.exports = main;


