import $ from 'ZEPTO_PATH';
const VARIABLE = require('UNIT_PATH/variable'); 

class LazySuccess{
	static loadLogoImg(img, wrapName) {
		var item        = $(img).closest(wrapName);
		var logo_img    = item.find('img[data-logo-layzr]');
		var logo_url    = logo_img.attr('data-logo-layzr');

		$(img).addClass('fadeIn');

		// 加载logo图片
		if(logo_url) {
			var Img = new Image();

			Img.onload = function() {
				logo_img.attr('src', logo_url);
			}

			Img.src = logo_url;
		}
	}
}

module.exports = LazySuccess;


